
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import { validateNotNull } from 'util/rules'
import Scroll from 'assembly/scroll/scroll'

export default {
data () {
return {
baseUrl: process.env.DOMAIN_IP,
show: false,
typeWay: '请选择充值方式',
typeName: '请选择款项用途',
typeMenu: {
'8796120907867': '货款',
'8796120940635': '保证金',
'8796121071707': '货款 + 保证金'
},
chargeType: [],
upChargeData: {
bank: '', // 支付方式
type: '', // 账户类型
balance: '', // 货款金额
deposit: '', // 保证金金额
remark: '' 
},
rate: 0,
top: 0,
typeSelected: false,
waySelected: false,
checked: false,
orderNo: '', // 从订单支付过来的orderNo
deposit: 0 
}
},
computed: {
serviceCharge () {
if (this.rate === 0) {
return this.top
} else {
let total = (Number(this.upChargeData.balance) + Number(this.upChargeData.deposit)) * this.rate
return this.top !== null && total > this.top ? Math.floor(this.top * 100) / 100 : Math.floor(total * 100) / 100
}
},
totalCharge () {
return Math.round((this.serviceCharge + Number(this.upChargeData.balance) + Number(this.upChargeData.deposit)) * 100) / 100
}
},
watch: {
'upChargeData.type' (newValue) {
let thiz = this
if (this.deposit > 0 && (newValue === '8796120940635' || newValue === '8796121071707')) {
this.$vux.confirm.show({
content: '你的账号已经有保证金' + this.deposit + '元，是否需要继续充值？',
onCancel () {
thiz.upChargeData.type = ''
thiz.typeName = '请选择款项用途'
},
onConfirm () {
}
})
}
}
},
methods: {
showSelector () {
this.typeSelected = true
},
showWaySelector () {
this.waySelected = true
},






selectBank (item) {
this.upChargeData.bank = item.key
this.top = item.top
this.rate = item.rate
},
setType (key, item) {
this.upChargeData.type = key
this.typeName = item
},
submit () {
if (!validateNotNull(this, this.upChargeData.bank, '充值方式')) {
return
}
let flag = false
if (validateNotNull(this, this.upChargeData.type, '款项用途')) {
flag = (this.upChargeData.type === '8796120907867' && validateNotNull(this, this.upChargeData.balance, '货款金额')) || (this.upChargeData.type === '8796120940635' && validateNotNull(this, this.upChargeData.deposit, '保证金金额')) ||
(this.upChargeData.type === '8796121071707' && validateNotNull(this, this.upChargeData.balance, '货款金额') && validateNotNull(this, this.upChargeData.deposit, '保证金金额'))
}
if (!flag) {
return
}
if (!this.checked) {
this.$vux.toast.text('请勾选充值提醒')
return
}
if (this.orderNo) {
this.upChargeData.orderNo = this.orderNo
}
this.$tupHttp(this, 'front/account/charge/online', 'POST', this.upChargeData).then(res => {
this.$util.rechargeResult(this.baseUrl + '/mobile-hsy/index.html#/rechargeResult?orderNo=' + res.data.model.orderNo, '充值结果')
if (res.data.model.method === 'GET') {
window.open(res.data.model.url, '_self')
} else {
this.createForm(res.data.model)
}
})
},
createForm (model) {
let tempForm = document.createElement('form')
tempForm.action = model.url
tempForm.target = '_self'
tempForm.method = 'post'
tempForm.style.display = 'none'
for (let x in model.params) {
let opt = document.createElement('textarea')
opt.name = x
opt.value = model.params[x]
tempForm.appendChild(opt)
}
document.body.appendChild(tempForm)
tempForm.submit()
},
_getInfo () {
this.$tupHttp(this, 'front/user/info/v1').then(res => {
this.deposit = res.data.model.userAccount.deposit / 100
})
},
_getChargeMethods () {
this.$tupHttp(this, 'front/account/charge/methods').then(res => {
this.chargeType = res.data.models.filter(item => {
return item.name.indexOf('工商') < 0
})








})
}
},
created () {
let amount = this.$route.query.amount
if (amount) {
this.upChargeData.balance = amount
this.upChargeData.type = '8796120907867'
this.typeName = '货款'
}
this.orderNo = this.$route.query.orderNo
this._getChargeMethods()
this._getInfo()
let u = navigator.userAgent
let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1
let isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
if (!isAndroid && !isIOS) {
this.show = true
}
},
components: {
XHeader,
Group,
XInput,
Cell,
Actionsheet,
Scroll,
XButton,
CheckIcon
}
}



